export default class Popup {
  constructor ({popupSelector}){
    this._popupSelector = popupSelector;
    this._popup = document.querySelector(this._popupSelector);
  }

  //Ð»Ð¾Ð³Ð¸ÐºÑ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° ÐºÐ»Ð°Ð²Ð¸ÑÐµÐ¹ Esc
  _handleEscClose = (event) => {
    if (event.key === 'Escape') {
      this.close();
    }
  }

  //Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ ÐºÐ»Ð¸ÐºÐ° Ð¸ÐºÐ¾Ð½ÐºÐµ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  setEventListeners () {
    this._popup.addEventListener('click', (event) => {
      if(event.target.classList.contains('popup_is_opened')){ 
        this.close();
        };
      if(event.target.classList.contains('popup-close')){ 
        this.close();
        };
      });
    }

  renderLoading (isLoading, text) {
    if(isLoading) {
      this._popup.querySelector('.popup-button').textContent = text;
    } else {
      this._popup.querySelector('.popup-button').textContent = text;
    }
  }

  //Ð¾ÑÐ²ÐµÑÐ°ÑÑ Ð·Ð° Ð¾ÑÐºÑÑÑÐ¸Ðµ Ð¸ Ð·Ð°ÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  close () {
    this._popup.classList.remove('popup_is_opened');
    document.removeEventListener('keydown', this._handleEscClose);
  }
  
  open () {
    this._popup.classList.add('popup_is_opened');
    document.addEventListener('keydown', this._handleEscClose);
  }
}

